    import {
        mapState,
        mapMutations
    } from 'vuex'

    export default {
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
		onLoad() {
		    if (!this.hasLogin) {
				uni.navigateTo({
				    url: '../home/home',
				});
			}
		},
		data(){
			return{
				index: -1,
				skin: false,
				picker: ['选择1', '选择2', '选择3'],
				date: '2018-12-25'
			}
		},
        methods: {
            ...mapMutations(['logout']),
			PickerChange(e) {
				this.index = e.detail.value
			},
			DateChange(e) {
				this.date = e.detail.value
			},
			
            bindLogin() {
                uni.navigateTo({
                    url: '../home/home',
                });
            },
            bindLogout() {
                this.logout();
				console.log(this.forcedLogin)
                /**
                 * 如果需要强制登录跳转回登录页面
                 */
                if (this.forcedLogin) {
                    uni.reLaunch({
                        url: '../home/home',
                    });
                } 
            },
			SwitchSex(e) {
				this.skin = e.detail.value
			}
        }
    }
